export default {
	data() {
		return {

		}
	},
	props: {
		value: String
	},
	methods: {
		back() { // 返回
			this.$emit("back")
		},
		change(e) { // input框发生改变时
			this.$emit('changeValue', e.detail.value)
		},
		seek() {
			this.$emit("add", this.value)
		}
	}
}
